@import 'normalize';
@import 'variables';
@import 'typography';
@import 'navigation';
// @import 'sidebar';
@import 'comments';
@import 'footer';
/*
====================================
Default Styling
====================================
*/

img {
	max-width: 100%;
}

body {
	color: #333;
	font-family: 'Open Sans', sans-serif;
}

.wrapper {
	max-width: 1200px;
	width: 80%;
	margin: 0 auto;
}

.about, .portfolio, .skills, .contact {
	padding: 50px 0;
}

.aboutContainer, .portfolioContainer, .skillContainer, .contactContainer {
	padding-top: 50px;
}

// HERO SECTION //
header {
	height: 100vh;
	.mainNav {
		width: 100%;
		padding: 20px;
		position: fixed;
		display: flex;
		justify-content: space-between;
		transition: all 0.8s ease;
		// HAMBURGER //
		.fa {
			display: none;
			cursor: pointer;
		}

		.fa-bars {
			position: relative;
			z-index: 10;
		}
		// MAIN NAV //
		h3 {
			// padding: 20px;
			visibility: hidden;
		}
		ul {
		    align-items: center;
			li {
				padding: 0 20px;
				a {
					padding: 10px;
					// background: rgba(255, 255, 255, 0);
					text-transform: uppercase;
					text-decoration: none;
					color: $white;
					font-size: 1.4rem;
					letter-spacing: 0.1rem;
					font-weight: 500;
					// text-shadow: rgba(128,128,128,0.7) 1px 1px 1px;
					&:hover {
						color: $orchid;
						background: $darkgray;
						border-radius: 2px;
						transition: all 0.3s ease;
					}
				}
			}
		}
	}
	.active {
	    padding: 10px 20px;
		background: $darkgray;
		transition: all 0.8s ease;
		z-index: 9;
		h3 {
			// padding: 20px;
			visibility: visible;
			color: $orchid;
			&:hover {
				color: $white;
				transition: all 0.3s ease;
			}
		}
		li {
			padding: 0 20px;
			a {
				padding: 10px;
				text-transform: uppercase;
				text-decoration: none;
				color: $white;
				font-size: 1.4rem;
				letter-spacing: 0.1rem;
				font-weight: 500;
				&:hover {
					color: $orchid;
					border-radius: 2px;
					transition: all 0.3s ease;
				}
			}
		}
	}
	.heroText {
		height: 90vh;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		h1 {
			font-family: 'Playfair Display', serif;
			font-size: 10rem;
		    color: $white;
		    font-weight: 400;
		    letter-spacing: 0.3rem;
		    text-shadow: rgba(128,128,128,0.7) 1px 1px 1px;
		    
		}
		h3 {
			color: $white;
		    font-weight: 500;
		    text-transform: uppercase;
		    letter-spacing: 0.2rem;
		    text-shadow: rgba(128,128,128,0.7) 1px 1px 1px;
		}
	}	
}

//ABOUT SECTION //
.about {
	.aboutContainer {
		display: flex;
		.headshot {
			width: 30%;
			img {
				width: 70%;
			}
		}
		.aboutDesc {
			width: calc(70% - 20px);
			margin-left: 20px;
		}
	}
}

// PORTFOLIO SECTION //
.portfolio {
	.portfolioContainer {
		display: flex;
		flex-wrap: wrap;
		.portfolioItem {
			width: 100%;
			margin: 20px;
			display: flex;
			box-shadow: rgba(128, 128, 128, 0.25) 1px 1px 0.5px;
			figure {
				max-width: 50%;
				img {
					width: 100%;
					display: block;
				}
			}
			.portfolioContent {
				width: 50%;
				padding: 30px 0;
				padding-left: 20px;
				display: flex;
				flex-direction: column;
			    justify-content: space-between;
				background: rgba(173, 133, 181, 0.2);
				ul {
					display: flex;
					flex-wrap: wrap;
					li {
						margin: 10px;
						padding: 10px;
						list-style-type: none;
						background: lighten($violet, 20);
						border-radius: 2px;
					}
					li:first-child {
						margin-left: 0;
					}
				}
			}
		}
		.portfolioItem:nth-child(even) {
		    flex-direction: row-reverse;
		    .portfolioContent {
				padding-right: 20px;
			}
		}
	}
}

// SKILLS SECTION //
.skills {
	background: linear-gradient(0deg, rgba(255,255,255,0.6), rgba(255,255,255,0.9)), url('images/shattered.png');
	// background: url('images/shattered.png');
	background-repeat: repeat;
	.skillContainer {
		display: flex;
		flex-wrap: wrap;
		.skill {
			width: calc(25% - 40px);
			margin: 20px;
			display: flex;
			flex-direction: column;
			span {
				margin: 0 auto;
				.devicons {
					font-size: 7rem;
					color: $orchid;
					text-shadow: rgba(128, 128, 128, 0.25) 1px 1px 0.5px;
				}
			}
			h4 {
			    padding-top: 10px;
			}
		}
	}
}

// CONTACT SECTION //
.contact {
	.contactContainer {
		display: flex;
		.contactInfo {
			width: 30%;
		    display: flex;
		    flex-direction: column;
		    h3 {
		    	margin-bottom: 25px;
		    	
		    }
		    span {
		    	padding-bottom: 35px;
		    	p {
		    		font-weight: 600;
		    	}
		    }
			.icon {
				width: 25px;
				margin-right: 20px;
				display: inline-block;
				text-align: center;
			}
			p {
			    display: inline-block;
			}
			.fa {
				color: $orchid;
				font-size: 2.5rem;
			}
		}
		.contactForm {
			width: 70%;
			form {
				.formInputs {
					display: flex;
					justify-content: space-between;
					p {
					    margin-top: 20px;
					}
					input[type=text], input[type=email] {
						width: 350px;
						height: 35px;
						margin-top: 5px;
						padding: 5px 10px;
						border: 2px solid $orchid;
					    border-radius: 2px;
					    font-size: 1.4rem;
					    line-height: 1.5rem;
					    color: $darkgray;
					}
				}
				.formMessage {
					p {
					    margin-top: 20px;
					}
					textarea {
						width: 100%;
						margin-top: 5px;
						height: auto;
						padding: 5px 10px;
						border: 2px solid $orchid;
					    border-radius: 2px;
					    font-size: 1.4rem;
					    line-height: 1.5rem;
					    color: $darkgray;
					}
					input[type=submit] {
						width: 150px;
						padding: 10px;
						align-self: flex-end;
						border: 2px solid $orchid;
						border-radius: 2px;
						background: rgba(255, 255, 255, 0);
						box-shadow: rgba(128, 128, 128, 0.25) 1px 1px 0.5px;
						color: $orchid;
						font-size: 1.6rem;
						transition: all 0.3s ease;
						&:hover {
							background: $orchid;
							color: $white;
						}
					}
				}
			}
		}
	}
}

// SOCIAL FOOTER //
.socialFooter {
	margin: 0 auto;
	background: linear-gradient(0deg, rgba(255,255,255,0.6), rgba(255,255,255,0.9)), url('images/shattered.png');
	background: url('images/shattered.png');
	background-repeat: repeat;
	ul {
		margin: 0 35%;
	    padding: 0;
	    display: flex;
	    justify-content: space-between;
	}
		li {
			padding: 10px;
			list-style-type: none;
		}
			.fa {
				padding: 10px;
				color: $orchid;
				border-radius: 2px;
				font-size: 2.5rem;
				&:hover {
					color: $white;
					background: $orchid;
				}
			}
}

footer {
	padding: 10px;
	background: $darkgray;
	color: $white;
	text-align: center;
}

/* Portrait tablet and small desktops */
@media (max-width: 940px) {
	.wrapper {
		width: 90%;
	}
	header {
		height: auto;

		// HAMBURGER MENU //
		ul#menu-main-navigation {
			position: fixed;
			top: 0;
			right: 0;
			height: 100%;
			width: 100%;
			background: rgba(255,255,255,0.8);
			opacity: 0;
			visibility: hidden;
			flex-flow: column wrap;
			text-align: center;
			justify-content: space-around;
			transition: all 0.5s;
			transform: scale(1.2);
			z-index: 99;
			li {
				padding: 15px 0;
				font-size: 2rem;
			}
				a {
					color: $orchid;
				}
		}
		ul#menu-main-navigation.show {
			opacity: 1;
			visibility: visible;
			transform: scale(1);
		}

		.fa {
			display: none;
			cursor: pointer;
		}

		.fa-bars {
			position: relative;
			z-index: 100;
		}
		// END HAMBURGER //
	}
	.about {
	    flex-direction: column;
	    align-items: center;
	    justify-content: space-around;
	    .aboutContainer {
	    	.headshot {
	    	    width: 100%;
	    	    margin: 0 auto;
	    	}
	    	.aboutDesc {
	    	    width: 100%;
	    	}
	    }
	}
	.portfolioItem {
	    flex-direction: column;
	    figure {
	    	width: 100%;
	    }
	    .portfolioContent {
	    	width: 100%;
		}
    }
	.contactContainer {
	    flex-direction: column;
	    .contactInfo {
		    width: 100%;
	    }
	    .contactForm {
	        width: 100%;

			form {
				.formInputs {
					input[type=text], input[type=email] {
						width: 300px;
					}
				}
				.formMessage {
					textarea {
						width: 100%;
						margin-top: 5px;
						height: auto;
						padding: 5px 10px;
						border: 2px solid $orchid;
					    border-radius: 2px;
					    font-size: 1.4rem;
					    line-height: 1.5rem;
					    color: $darkgray;
					}
				}
			}
		}
	}
}

